* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  font-size: 20px;
  @media all and  (max-width: 720px)  {    
    font-size: 16px;
  }
}

h2 {
  font-size: 30px;
  padding-bottom: 30px;
  @media all and (max-width: 720px) {    
    padding-bottom: 0;
    font-size: 20px;
  }
}

.previewExample {
  padding: 30px;
}

.previewTitle {
  padding: 30px 0 30px 0;
}

body {
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 35px;
  background-color:lightgray;
  display: flex;
  justify-content: center;
  align-items: center;
}
.medicationCard{
  padding:30px;
  width: 600px;
  margin-top:20px;

  background-color:rgb(243, 241, 241);
  display: flex;
  flex-direction: column;
  align-items: left;
  @media all and (max-width: 720px) {    
    width: 100%;
  }
}

.day {
  width: 100%;
  height: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.hour {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  font-size: 10px;
  border-radius: 20px;
  margin-left: 10px;
}

.listItemPreview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}

.colorPreview {
  width: 30px;
  height: 30px;
  border-radius: 20px;
  border: 1.5px solid black;
  font-size: 10px;
}

.namePreview {
  list-style-type: none;
  padding: 0 20px 0 20px;
}

.example__container{
  padding: 30px;
}

.example__time--container{
  width:250px;
  display: flex;
  justify-content: space-between;
  @media all and (max-width: 400px) {    
    width: 100%;
  }
}




.timePreview {
  padding-left: 20px;
}

.blue {
  background-color: blue;
}

.yellow {
  background-color: yellow;
}

.red {
  background-color: red;
}

.green {
  background-color: green;
}

.purple {
  background-color: purple;
}

.orange {
  background-color: orange;
}

.medicationContainer {
  padding: 30px;
}

.medicationContainer h2 {
  padding-bottom: 10px;
}

footer {
  padding: 30px;
  text-align: center;
}

